@import url('https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,500;1,400;1,500&display=swap');

body {
    font-family: 'Karla', sans-serif;
  }

header {
    position: fixed; /* pour fixer le header pendant le scroll */
    width: 100%;
    /* background-color: rgb(58, 118, 135); */
    /* color: whitesmoke; */
}


/**
 * NAV
 */
nav{
  padding: 1rem;
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  overflow: auto;
  background: white;
  z-index: 2;
  /* background: rgba(240,240,240,1); */
}
nav ul{
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch;
  width: calc(100% - 2rem);
}

nav li:not(:last-of-type){
  margin-right: .5rem;
}

nav a{
  color: inherit;
  text-decoration: none;
}

nav .navitem-container a {
  display: block;
}

nav .navitem-container a.active {
  font-weight: 800;
}


.lang-toggle {
  display: flex;
  flex-flow: column nowrap;
  border-left: 1px solid black;
  padding-left: .5rem;
}

.lang-toggle button:not(.is-active) {
  cursor: pointer;
}

.lang-toggle button.is-active {
  font-weight: 800;
}


/**
 * FOOTER 
 */

footer {
    /* position: fixed; */
    background-color: thistle;
}

#wrapper {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-content: stretch;
    /* je voudrais que mon header s'éloigne du reste mais n'y arrive pas pour l'instant */
}

.HeaderNav {
    display: flex; 
    justify-content: space-around;
    flex-direction: row;
  }

#wrapper > main {
    display: flex;
    padding: 1rem;
    padding-top: 6rem;
    justify-content: stretch;
    align-content: stretch;
    /* je voudrais que mon header s'éloigne du reste mais n'y arrive pas pour l'instant */
}


/**
 * MISC
 */

 button {
  border: none;
  background: rgba(220,220,220,1);
  padding: .5rem;
  font-family: 'Karla', sans-serif;
  outline: none;
  cursor: pointer;
}

button.is-active {
  background: rgba(50,50,50,1);
  color: white;
}